.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
}
.button-action {
  color: #ffffff;
  background-color: #68b738;
}
.button-action:hover,
.button-action:focus {
  background-color: #5eaa2f;
}
.button-action:active {
  color: rgba(255, 255, 255, 0.3);
}
.input-text {
  border: 1px solid #e7e7e7;
  height: 45px;
  width: auto;
  padding-left: 15px;
  padding-right: 15px;
  text-transform: uppercase;
}
.input-text:hover,
.input-text:hover:placeholder-shown {
  border: 1px solid #cdcdcd;
}
.input-text:invalid {
  border: 2px solid #ff8282;
}
.input-text:placeholder-shown {
  border: 1px solid #e7e7e7;
}
.input-text:focus,
.input-text:focus:placeholder-shown {
  outline: none;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .input-text {
    height: 52px;
    padding-left: 16px;
  }
}
.input-text::placeholder {
  text-transform: uppercase;
  color: #444444;
}
.input-radio ~ label::before {
  content: '';
  position: absolute;
  top: calc(50% - 15px);
  left: -46px;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .input-radio ~ label::before {
    top: calc(50% - 20px);
    left: -60px;
    width: 40px;
    height: 40px;
  }
}
.input-radio ~ label:hover::before {
  border-color: #bababa;
}
.input-radio:checked ~ label::after {
  content: '';
  position: absolute;
  top: calc(50% - 7px);
  left: -38px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .input-radio:checked ~ label::after {
    top: calc(50% - 9px);
    left: -49px;
    width: 18px;
    height: 18px;
  }
}
.input-radio:disabled ~ label {
  opacity: 0.3;
}
.input-radio:disabled ~ label::before {
  border-color: #bababa;
}
.input-checkbox ~ label::before {
  content: '';
  position: absolute;
  top: calc(50% - 15px);
  left: -46px;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .input-checkbox ~ label::before {
    top: calc(50% - 20px);
    left: -60px;
    width: 40px;
    height: 40px;
  }
}
.input-checkbox ~ label:hover::before {
  border-color: #bababa;
}
.input-checkbox:checked ~ label::after {
  content: '';
  position: absolute;
  top: calc(50% - 14px);
  left: -44px;
  width: 26px;
  height: 26px;
  background-image: url("../img/icons/tick.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .input-checkbox:checked ~ label::after {
    left: -53px;
  }
}
.input-checkbox:disabled ~ label {
  opacity: 0.3;
}
.input-checkbox:disabled ~ label::before {
  border-color: #bababa;
}
.textarea {
  border: 1px solid #e7e7e7;
  padding: 9px 5px 13px 15px;
  text-transform: uppercase;
  resize: none;
}
.textarea:hover {
  border: 1px solid #cdcdcd;
}
.textarea:focus {
  outline: none;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .textarea {
    padding: 13px 5px 13px 18px;
  }
}
.textarea::placeholder {
  text-transform: uppercase;
  color: #444444;
}
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  min-width: 280px;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
  overflow-x: hidden;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
@media (min-width: 1440px) {
  .page__body--background {
    background-image: linear-gradient(to right, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85) 720px, #68b738 900px), url("../img/background/page-background-desktop@1x.jpg"), linear-gradient(to right, #68b738, #68b738);
    background-position: 50vw top;
    background-size: auto 694px;
    background-repeat: no-repeat;
    z-index: -1;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page__body--background {
    background-image: linear-gradient(to right, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85) 720px, #68b738 900px), url("../img/background/page-background-desktop@2x.jpg"), linear-gradient(to right, #68b738, #68b738);
  }
}
