.main-nav {
  position: relative;
}
@media (min-width: 1440px) {
  .main-nav {
    max-width: 600px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: -45px;
  left: calc(50vw + 120px);
  display: block;
  width: 18px;
  height: 18px;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: inherit;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened {
    padding-bottom: 2px;
  }
  .main-nav--closed {
    border-bottom: 1px solid #d9d9d9;
  }
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav__item {
    border-top: 1px solid #e6e6e6;
  }
  .main-nav__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: '';
  position: absolute;
  top: -2px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 6px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
}
.main-nav__item {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  transition: all 0.75s ease-out;
}
@media (min-width: 768px) {
  .main-nav__item {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    margin-left: 35px;
  }
  .main-nav__item:first-child {
    margin-left: 0;
  }
  .main-nav__item--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
  .page__body--background .main-nav__item {
    color: #ffffff;
  }
  .page__body--background .main-nav__item--current {
    border-bottom: 2px solid #ffffff;
  }
}
.main-nav__link {
  display: block;
  padding-top: 22px;
  padding-bottom: 21px;
  text-transform: uppercase;
}
.main-nav__link[href]:hover,
.main-nav__link[href]:focus {
  opacity: 0.6;
}
.main-nav__link[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding-top: 18px;
    padding-bottom: 8px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    padding-top: 16px;
    padding-bottom: 6px;
  }
}
